/*  Сглаживание шрифтов *Safari
  ------------------------------------ */
html,
body,
button {
  -moz-osx-font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6, p, .fake-h4, .fake-h3, .flatlist__item__container__landlord__nam, .flatlist__item__header {
  margin: 0;
}

ul, ol, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

html {
  height: 100%;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  font-family: roboto-400, sans-serif;
  font-size: 16px;
  color: #565656;
  line-height: 1.5;
  background-color: #fff;
  position: relative;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

@media screen and (min-width: 481px) {
  body {
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
  body::-webkit-scrollbar {
    width: 5px;
  }
  body::-webkit-scrollbar-track {
    width: 5px;
  }
  body::-webkit-scrollbar-thumb {
    background-color: #565656;
    border-radius: 100px;
  }
}

input, textarea, select {
  outline: none;
  appearance: none;
  -webkit-appearance: none;
  box-shadow: none;
  background-color: transparent;
}

input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder, select:-ms-input-placeholder {
  font-family: roboto-300, sans-serif;
  font-size: 14px;
  color: #808080;
}

input:-moz-placeholder, textarea:-moz-placeholder, select:-moz-placeholder {
  font-family: roboto-300, sans-serif;
  font-size: 14px;
  color: #808080;
}

input::-moz-placeholder, textarea::-moz-placeholder, select::-moz-placeholder {
  font-family: roboto-300, sans-serif;
  font-size: 14px;
  color: #808080;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder, select::-webkit-input-placeholder {
  font-family: roboto-300, sans-serif;
  font-size: 14px;
  color: #808080;
}

input::placeholder, textarea::placeholder, select::placeholder {
  font-family: roboto-300, sans-serif;
  font-size: 14px;
  color: #808080;
}

/*
 * disable auto-zoom on iphone input field focus 
 * http://www.456bereastreet.com/archive/201212/ios_webkit_browsers_and_auto-zooming_form_controls/
*/
input[type='text']:focus,
input[type='number']:focus,
textarea:focus {
  font-size: 16px;
}

/* disable auto-zoom on iphone input field focus */
@media screen and (-webkit-min-device-pixel-ratio: 0) {
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
  }
}

input[type="num"], input[type="number"], input[type="tel"], input[type="time"], input[type="date"] {
  font-family: oswald-500, sans-serif;
  letter-spacing: .5px;
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: textfield;
}

input[type="num"]::-webkit-outer-spin-button, input[type="num"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button, input[type="number"]::-webkit-inner-spin-button, input[type="tel"]::-webkit-outer-spin-button, input[type="tel"]::-webkit-inner-spin-button, input[type="time"]::-webkit-outer-spin-button, input[type="time"]::-webkit-inner-spin-button, input[type="date"]::-webkit-outer-spin-button, input[type="date"]::-webkit-inner-spin-button {
  appearance: none;
}

select {
  width: 100%;
}

.is-hidden {
  display: none !important;
}

.hidden {
  display: none;
}

.lg-only, .md-only, .sm-only, .xs-only {
  display: none;
}

