.bg--white {
  background-color: #fff;
}

.bg--green {
  background-color: #8cc152;
}

.range {
  *zoom: 1;
}

.range:after {
  content: " ";
  display: table;
  clear: both;
}

.range__slider {
  margin-top: 5px;
  margin-bottom: 15px;
}

.range__from, .range__to {
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  font-family: oswald-400, sans-serif;
  color: #808080;
}

.range__from span, .range__to span {
  font-size: 14px;
  color: #565656;
}

.range__from i, .range__to i {
  font-size: 13px;
  color: #e1e1e1;
}

.range__from {
  float: left;
}

.range__to {
  float: right;
}

.star-rating {
  font-size: 0;
}

.star-rating__wrap {
  display: inline-block;
  font-size: 16px;
}

.star-rating__wrap::after {
  content: "";
  display: table;
  clear: both;
}

.star-rating__ico {
  float: right;
  padding-left: 2px;
  cursor: pointer;
  color: #FFB300;
}

.star-rating__ico:last-child {
  padding-left: 0;
}

.star-rating__input {
  display: none;
}

.star-rating__ico:hover:before,
.star-rating__ico:hover ~ .star-rating__ico:before,
.star-rating__input:checked ~ .star-rating__ico:before {
  content: "\f005";
}

.fancybox-slide--html {
  padding: 5px 5px 0;
}

.fancybox-container {
  z-index: 1001;
}

.compensate-for-scrollbar {
  margin-right: 0px !important;
}

.fancybox-slide--html .fancybox-content {
  margin-bottom: 0;
}

.fancybox-show-caption .fancybox-caption, .fancybox-show-infobar .fancybox-infobar, .fancybox-show-toolbar .fancybox-toolbar {
  display: none;
}

.fancybox-can-drag .fancybox-content {
  cursor: default;
}

.fancybox-slide {
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.fancybox-slide::-webkit-scrollbar {
  width: 5px;
}

.fancybox-slide::-webkit-scrollbar-track {
  width: 5px;
}

.fancybox-slide::-webkit-scrollbar-thumb {
  background-color: #565656;
  border-radius: 100px;
}

.modal-window {
  min-width: 320px;
  margin-right: -3px;
  padding: 0 !important;
  border-radius: 2px;
  box-shadow: 0 1px 60px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

@media screen and (max-width: 320px) {
  .modal-window {
    min-width: 300px;
  }
}

.modal-window--mobile {
  max-width: 425px;
  width: 100%;
  padding: 0;
}

@media screen and (max-width: 425px) {
  .modal-window--mobile {
    width: 414px;
  }
}

@media screen and (max-width: 375px) {
  .modal-window--mobile {
    width: 355px;
  }
}

@media screen and (max-width: 320px) {
  .modal-window--mobile {
    width: 320px;
  }
}

.modal-window__wrap .fancybox-close-small:hover::after {
  color: #000;
  background-color: transparent;
}

.modal-window__wrap .fancybox-close-small::after {
  font-size: 24px;
  color: #e6e6e6;
  background-color: transparent;
  top: 7px;
  right: 4px;
}

.modal-window__body {
  padding: 20px 20px;
}
